<template> 
  <div class="app-container" style="width: 80%;margin: auto">
    <div class="table-container" >

      <el-card shadow="never" style="">
        <div slot="header">
          <svg-icon icon-class="marker" style="color: #606266"></svg-icon>
          <span class="font-small">用户修改信息</span>
        </div>
        <el-form :model="user" :rules="rules" ref="userInfoForm"  label-width="20%" style="width: 60%;" size="small" hide-required-asterisk>
          <el-form-item label="旧密码：" prop="oldPass">
            <el-input v-model="user.oldPass" show-password ></el-input>
          </el-form-item>
          <el-form-item label="新密码：" prop="newPass">
            <el-input v-model="user.newPass" show-password></el-input>
          </el-form-item>
          <el-form-item label="确认新密码：" prop="confirmPass">
            <el-input v-model="user.confirmPass" show-password></el-input>
          </el-form-item>
          <el-form-item style="text-align: center">
            <el-button type="primary" size="medium" :loading="updateLoading" :disabled="updateLoading" @click="handleModifyUser('userInfoForm')">修 改</el-button>
          </el-form-item>
        </el-form>
      </el-card>

    </div>

  </div>
</template>
<script>
  import {addOrder} from "@/api/order";
  import {updateUser} from "@/api/user";
  import {mapGetters} from "vuex";
  export default {
    name: 'modifyUser',
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.user.confirmPass !== '') {
            this.$refs.userInfoForm.validateField('confirmPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.user.newPass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {

        defaultUser:{
          username: '',
          oldPass: '',
          newPass: '',
          confirmPass: ''
        },
        user: {},
        rules: {
          oldPass:  [{required: true, message: '请输入原密码', trigger: 'blur'}],
          newPass: [{validator: validatePass, trigger: 'blur'}],
          confirmPass: [{validator: validatePass2, trigger: 'blur'}]
        },
        updateLoading: false
      }
    },
    created() {
      this.handleResetUser();
    },
    computed: {
      ...mapGetters([
        'name'
      ])
    },
    methods: {
      handleResetUser() {
        this.user =  Object.assign({}, this.defaultUser);
        this.user.username = this.name;
      },

      handleModifyUser(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {

              this.updateLoading = true;
              updateUser(this.user).then(response => {

                this.$message({
                  message: '保存成功',
                  type: 'success',
                  duration: 1000
                });

                this.handleResetUser();
                this.updateLoading = false;

              }).catch(() => this.updateLoading = false);

          }
        });
      },
    }
  }
</script>
<style>
</style>



